html, body, #root {
  height: 100vh; }

body {
  font-family: Poppins, "Helvetica Neue", Arial, Helvetica, sans-serif;
  font-weight: lighter;
  font-size: 1rem;
  color: #222222; }

h1, h2, h3, h4, h5, h6 {
  margin: 1em 0;
  font-weight: 300; }

a {
  text-decoration: none;
  color: inherit; }

img {
  max-width: 100%; }

.form {
  display: grid;
  grid-auto-flow: row;
  grid-gap: 1em;
  align-content: center;
  background: white;
  padding: 5em 4em;
  box-shadow: 0 0 0 6px rgba(90, 105, 120, 0.3);
  color: #5A6978; }
  .form .form-title {
    font-weight: bolder;
    margin-top: 0;
    margin-bottom: 2em; }
  .form .form_label {
    font-size: 0.8em;
    display: block; }
  .form .form_input {
    color: black;
    padding: 1em 0;
    border: 0;
    outline: 0;
    border-bottom: 1px solid #5A6978;
    width: 100%;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
    border-radius: 0; }
  .form .form_group > .form_label {
    margin-bottom: 1em; }
  .form .form_fields_group--radio {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1em; }
  .form .form_field--radio {
    position: relative; }
    .form .form_field--radio .form_label {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0.5em; }
    .form .form_field--radio .form_input {
      grid-area: 'item';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      opacity: 0;
      left: 0; }
      .form .form_field--radio .form_input:checked ~ label {
        outline: 1px solid;
        background: #eee; }
  .form .form_button_field input {
    border: 0;
    background: #283552;
    color: white;
    padding: 1em 2em;
    cursor: pointer;
    margin-left: auto; }
    .form .form_button_field input:active {
      -webkit-transform: translateY(1px);
              transform: translateY(1px); }
  .form .form_message {
    text-align: left;
    padding: 1em 0;
    margin: 0;
    display: block;
    line-height: 1.5em;
    font-size: 0.8em; }
    .form .form_message.link {
      cursor: pointer; }
      .form .form_message.link:hover {
        text-decoration: underline; }
    .form .form_message.info {
      padding: 1em 0;
      text-align: center; }
    .form .form_message.error {
      padding: 1em;
      font-size: 0.8em;
      text-align: center;
      color: indianred;
      background: #f1d1d1; }

.app {
  padding: 4em;
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr auto 1fr;
  grid-gap: 4em;
  align-items: start;
  height: 100%; }
  .app .form {
    width: 450px; }
  .app > section {
    height: 100%;
    overflow: auto; }

